"use strict";

document.addEventListener("DOMContentLoaded", function(){
    iniciarApp();
});

function iniciarApp(){
    navegacionFija();
    crearGaleria();
}

function navegacionFija(){
    const header = document.querySelector(".header");
    const sobreFestival = document.querySelector(".sobre-festival");
    const body = document.querySelector("body");

    window.addEventListener("scroll",function(){
        if(sobreFestival.getBoundingClientRect().top < 0){
            header.classList.add("fijo");
            body.classList.add("body-scroll");
        }else{
            header.classList.remove("fijo");
            body.classList.add("body-scroll");
        }
    });
}

//Menu Header
let menuActivo = false;
const navMenu = document.querySelector(".navMenu");
const botonMenu = document.querySelector(".butonMenu");

botonMenu.addEventListener("click",() => {
    if(!menuActivo){
        navMenu.classList.add("visible");
        botonMenu.setAttribute("name","close");
        menuActivo = true;
    }else{
        navMenu.classList.remove("visible");
        botonMenu.setAttribute("name","menu");
        menuActivo = false;
    }
})

//Galeria

function crearGaleria(){
    const galeria = document.querySelector(".galeria-imagenes");
    
    for(let i = 1; i <= 12;i++){
        const imagen = document.createElement("picture");
        imagen.innerHTML =`<source srcset=\"build/img/thumb/${i}.avif\" type=\"image/avif\">
                            <source srcset=\"build/img/thumb/${i}.webp\" type=\"image/webp\">
                            <img loading=\"lazy\" width=\"200\" height=\"300\" src=\"build/img/thumb/${i}.jpg\" alt=\"Imagén de Galería número ${i}\">`;

        imagen.onclick = function (){
            mostrarImagen(i);
        }

        galeria.appendChild(imagen);
    }
}

function mostrarImagen(id){
    let imagen = document.createElement("picture");
        imagen.innerHTML =`<source srcset=\"build/img/grande/${id}.avif\" type=\"image/avif\">
                            <source srcset=\"build/img/grande/${id}.webp\" type=\"image/webp\">
                            <img loading=\"lazy\" width=\"200\" height=\"300\" src=\"build/img/grande/${id}.jpg\" alt=\"Imagén de Galería número ${id}\">`;

    const botonIzquierda = document.createElement("P");
    botonIzquierda.textContent = "<";
    botonIzquierda.classList.add("boton");
    botonIzquierda.onclick = function(){
        if(id > 1){
            --id;
            imagen.innerHTML =`<source srcset=\"build/img/grande/${id}.avif\" type=\"image/avif\">
                                <source srcset=\"build/img/grande/${id}.webp\" type=\"image/webp\">
                                <img loading=\"lazy\" width=\"200\" height=\"300\" src=\"build/img/grande/${id}.jpg\" alt=\"Imagén de Galería número ${id}\">`;
        }
    }
    const botonDerecha = document.createElement("P");
    botonDerecha.textContent = ">";
    botonDerecha.classList.add("boton")
    botonDerecha.onclick = function(){

        if(id< 12){
            ++id;
            imagen.innerHTML =`<source srcset=\"build/img/grande/${id}.avif\" type=\"image/avif\">
                                <source srcset=\"build/img/grande/${id}.webp\" type=\"image/webp\">
                                <img loading=\"lazy\" width=\"200\" height=\"300\" src=\"build/img/grande/${id}.jpg\" alt=\"Imagén de Galería número ${id}\">`;
        }
    }

    const panelImagen = document.createElement("DIV");
    panelImagen.appendChild(botonIzquierda);
    panelImagen.appendChild(imagen);
    panelImagen.appendChild(botonDerecha);
    panelImagen.classList.add("panelImagen");

    const overlay = document.createElement("DIV");
    overlay.appendChild(panelImagen);
    overlay.classList.add("overlay");
    
    const cerrarModal = document.createElement("P");
    cerrarModal.textContent = "x";
    cerrarModal.classList.add("boton");
    cerrarModal.onclick = function(){
        const body = document.querySelector("body");
        body.classList.remove("fijarBody");
        overlay.remove();
    } 

    overlay.appendChild(cerrarModal);

    const body = document.querySelector("body");
    body.appendChild(overlay);
    body.classList.add("fijarBody");
}